<template>
  <div class="card">
    <div class="img">
      <img :src="user.headimgurl" alt="">
      <span class="lv">{{user.empirical_value}}</span>
    </div>
    <div class="infos">
      <h3>{{user.user_name}}</h3>
      <p style="margin: 0;    padding-left: 1rem;">
        <img
          v-for="(item, index) in user.medal"
          :key="index"
          :src="item.imgurl"
          :title="item.name"
          :alt="item.content"
          style="cursor: pointer; width: 22px;height: 33.25px;vertical-align: middle;"
        >
      </p>
      <div class="item">
        ID: {{user.user_id}} / {{user.last_time}}
      </div>
      <div class="item">
        <span>{{user.amount}}</span>篇帖子 • <span>{{user.highlight}}</span>篇精华
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home-User-Info-Card',
  props: {
    user: ''
  }
}
</script>

<style lang="less" scoped>
  .card{
    display: flex;
    padding: 15px;
    background-color: #fff;
    .img{
      width: 4rem;
      height: 4rem;
      text-align: center;
      img{
        width: 85%;
        height: 85%;
        border-radius: 50%;
      }
      span.lv{
        font-size: 13px;
        font-weight: 400;
        border-radius: 2px;
        padding: 2px 4px;
        background-color: #81d573;
        color: #188909;
      }
    }
    .infos{
      flex: 1;
      font-size: 14px;
      margin-left: 1rem;
      overflow: hidden;
      background-color: #eee;
      h3{
        margin-top: 5px;
        margin-bottom: 5px;
        padding-left: 1rem;
      }
      .item{
        padding-left: 1rem;
        margin-bottom: 5px;
        color: #999;
        span{
          color: #666;
        }
      }
    }
  }
</style>



