<template>
  <div class="reply">
    <div class="title">
      <router-link :to="'/forum/posts/' + reply.forum_id">{{reply.title}}</router-link>
      <span>{{reply.create_time}}</span>
    </div>
    <div class="cont" v-html="reply.content"></div>
  </div>
</template>

<script>
export default {
  name: 'Forum-Home-Reply',
  props: {
    reply: Object
  }
}
</script>

<style lang="less" scoped>
  .reply{
    border-radius: 0!important;
    position: relative;
    padding: 7px;
    display: block;
    border-bottom: 1px solid #eee;
    background-color: #fff;
    .title{
      font-size: 15px;
      a {
        color: #333;
      }
      span{
        font-size: 12px;
        color: #bbb;
      }
    }
    .cont{
      margin-top: 6px;
      color: #666;
    }
  }
</style>



