<template>
  <div class="home-wrap">
    <forum-header
      :labels="labels"
    ></forum-header>
    <div class="home-main">
      <forum-user-card
        :user="user"
      ></forum-user-card>
      <div class="main">
        <mt-navbar v-model="selected" style="margin-top: 10px;">
          <mt-tab-item id="posts">帖子</mt-tab-item>
          <mt-tab-item id="reply">回帖</mt-tab-item>
          <mt-tab-item id="unaudit" v-show="user.mine">待审核</mt-tab-item>
          <mt-tab-item id="reject" v-show="user.mine">审核失败</mt-tab-item>
        </mt-navbar>
        <mt-tab-container v-model="selected" style="margin-top: 10px;">
          <mt-tab-container-item id="posts">
            <forum-home-posts
              v-for="(item, index) in postsList"
              :key="index"
              :posts="item"
            ></forum-home-posts>
          </mt-tab-container-item>
          <mt-tab-container-item id="reply">
            <forum-home-reply
              v-for="(item, index) in replyList"
              :key="index"
              :reply="item"
            ></forum-home-reply>
          </mt-tab-container-item>
          <mt-tab-container-item id="unaudit" v-show="user.mine">
            <forum-home-posts
              v-for="(item, index) in applyList"
              :key="index"
              :posts="item"
            ></forum-home-posts>
          </mt-tab-container-item>
          <mt-tab-container-item id="reject" v-show="user.mine">
            <forum-home-posts
              v-for="(item, index) in rejectedList"
              :key="index"
              :posts="item"
            ></forum-home-posts>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </div>
  </div>
</template>

<script>
import ForumHeader from '../forum-header.vue'
import ForumUserCard from './home-user-info-card.vue'
import ForumHomePosts from './home-posts.vue'
import ForumHomeReply from './home-reply.vue'
import {getLocalStorage} from '../../../lib/util'
import {getPersonCenterData, getPersonCenterPostsList} from '../../../api/forum'
import {Toast} from 'mint-ui'
export default {
  name: 'Forum-Home',
  data () {
    return {
      labels: [],
      postsList: [],
      replyList: [],
      applyList: [],
      rejectedList: [],
      user: '',
      selected: 'posts'
    }
  },
  created () {
    this.getLabels()
    this.getDatas()
  },
  methods: {
    getLabels () {
      this.$set(this, 'labels', JSON.parse(getLocalStorage('labels')))
    },
    getDatas () {
      getPersonCenterData(this.$route.params.id)
        .then(data => {
          if (data.state) {
            this.$set(this, 'user', data.data.info)
          } else {
            Toast(data.errormsg || '获取数据失败')
          }
        })
      getPersonCenterPostsList(this.$route.params.id)
        .then(data => {
          if (data.state) {
            this.$set(this, 'postsList', data.data.list)
          } else {
            Toast(data.errormsg || '获取数据失败')
          }
        })
      getPersonCenterPostsList(this.$route.params.id, true)
        .then(data => {
          if (data.state) {
            this.$set(this, 'replyList', data.data.list)
          } else {
            Toast(data.errormsg || '网络错误')
          }
        })
      getPersonCenterPostsList(this.$route.params.id, undefined, true)
        .then(data => {
          if (data.state) {
            this.$set(this, 'applyList', data.data.list)
          } else {
            Toast(data.errormsg || '网络错误')
          }
        })
      getPersonCenterPostsList(this.$route.params.id, undefined, undefined, true)
        .then(data => {
          if (data.state) {
            this.$set(this, 'rejectedList', data.data.list)
          } else {
            Toast(data.errormsg || '网络错误')
          }
        })
    }
  },
  components: {
    ForumHeader,
    ForumUserCard,
    ForumHomePosts,
    ForumHomeReply
  }
}
</script>

<style lang="less" scoped>
  .home-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .home-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
    }
  }
</style>



