<template>
  <div class="posts">
    <div class="label-name" v-show="posts.cn">
      <router-link :to="'/forum/list/' + posts.label_id">{{posts.cn}}</router-link>
    </div>
    <div class="cont">
      <router-link :to="'/forum/posts/' + posts.forum_id">
        <h2>{{posts.title}}</h2>
      </router-link>
      <div class="info">
        发帖时间: {{posts.create_time}} • 查看: {{posts.browse}} • 回复: {{posts.reply_amount}}
      </div>
      <div class="info" v-show="posts.reason">
        审核未通过原因: {{posts.reason}}
      </div>
      <router-link v-show="posts.state === 4" :to="'/forum/create/edit?forum_id=' + posts.forum_id" style="float: right">修改</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Forum-Home-Posts',
  props: {
    posts: Object
  }
}
</script>

<style lang="less" scoped>
  .posts {
    display: flex;
    background-color: #fff;
    border-bottom: 1px solid #eee;
    .label-name{
      width: 4.4rem;
      padding: 7px;
      a{
        color: #333;
      }
      /*overflow: hidden;*/
    }
    .cont{
      flex: 1;
      padding: 7px;
      a{
        display: block;
        color: #333;
        h2{
          margin: 0;
          font-size: 14px;
          font-weight: 400;
        }
      }
      .info{
        color: #999;
        font-size: 12px;
      }
    }
  }
</style>



